.pricing{
    width:100%;
    padding: 6rem 1rem;
    background: #f5f5f4;
}

.card-container{
    max-width:1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
   
}

.card{
    border: 1px solid #eee;
    color: #eee;
    text-align: center;
    padding: 1rem;
    background-color: #000;
}

.card:hover{
    background-color: #141414;
}

.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}

.card p{
    padding: 10px 0;

}

.bar{
    border-bottom: 1px solid #eee;
    width: 10%;
    margin: 1.5rem auto;
    display: block;
}

.btc{
    font-size: 3.5rem;
    font-weight: 600;
    padding: 1 rem 0;
}

.bt{
    display: block;
    width: 80%;
    margin: 2rem auto;
    
}

@media screen and (max-width: 740px){
    .card-container{
        max-width:90%;
        margin: auto;
        display: grid;
        grid-template-columns: 1fr;
    }
        
}